import { mapGetters } from 'vuex'
export default {
    computed: {
        ...mapGetters('card', ['getCart', 'getTotalPrice']),
    },
    data: function () {
        return {
            inputValue: '',
            telephoneValue: '',
        }
    },

    methods: {
        showAlert: function () {
            this.$toast('Оплата временно недоступна', {
                timeout: 2000,
                position: 'bottom-center',
                icon: false,
                hideProgressBar: true,
            })
        },
    },
}
